.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#prev {
  position: absolute;
  top: 80%;
  z-index: 1;
  left: 100px;
}
#next {
  top: 80%;
  position: absolute;
  z-index: 1;
  right: 100px;
}
body {
  background-color: #293462;
}
button {
  color: #fff;
  background-color: #ffa500;
  padding: 5px 30px;
  border-radius: 5px;
  font-size: 0.8rem;
  font-weight: 500;
  width: 100px;
  cursor: pointer;
}
button:hover {
  background-color: #cf8c10;
}
