@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@700&display=swap);
.Option_main {
  margin: 5px auto;
  border-radius: 5px;
  text-align: center;
  padding: 15px;
  min-width: 300px;
}
.Option_main:focus {
  padding: 30px 15px;
}

.main__div {
  position: relative;
  display: flex;
}

.page1 {
  color: #fff;
  width: 100%;
  height: 100vh;
  background-color: #090f23;
  position: absolute;
}
.page1 p {
  font-size: 1.5rem;
}
.page1 p:last-child {
  font-size: 0.7rem;
  margin: 27px;
}
a {
  color: #39acb4;
}
.page1 input {
  border-radius: 5px;
  width: 270px;
  padding: 12px 15px;
  outline: none;
  border: none;
}
::-webkit-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  font-size: 0.95rem;
  opacity: 0.9; /* Firefox */
}
::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  font-size: 0.95rem;
  opacity: 0.9; /* Firefox */
}
.page1 input:active {
  outline: none;
  border: none;
}
.page1 button {
  width: 300px;
  margin-top: 10px;
  border-radius: 5px;
  border: none;
  padding: 10px;
  color: #fff;
  background-color: #ffa500;
  font-size: 1rem;
  font-weight: 700;
}
.page1 button:hover {
  color: #fff;
  background-color: #cf8c10;
  cursor: pointer;
}
.page1 span {
  color: #39acb4;
  font-family: "Quicksand", sans-serif;
}
.page2 {
  color: #fff;
  width: 100%;
  height: 100vh;
  background-color: #090f23;

  display: none;
  position: absolute;
}
.page3 {
  color: #fff;
  width: 100%;
  height: 100vh;
  background-color: #090f23;
  position: absolute;
  display: none;
}
.page4 {
  color: #fff;
  width: 100%;
  height: 100vh;
  background-color: #090f23;
  position: absolute;
  display: none;
}
.innerPage {
  max-width: 350px;
  margin: 0 auto;
  padding: 50px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
#prev {
  position: absolute;
  top: 80%;
  z-index: 1;
  left: 100px;
}
#next {
  top: 80%;
  position: absolute;
  z-index: 1;
  right: 100px;
}
body {
  background-color: #293462;
}
button {
  color: #fff;
  background-color: #ffa500;
  padding: 5px 30px;
  border-radius: 5px;
  font-size: 0.8rem;
  font-weight: 500;
  width: 100px;
  cursor: pointer;
}
button:hover {
  background-color: #cf8c10;
}

